<template>
	<div class="wrapper">
		<div class="dashboard__header">
			<div class="dashboard__title">
				{{ activeDashboardTitle }}
			</div>
			<div class="dashboard__control">
				<button-vue
					:color="'gray'"
					:active="activeNestedDashboardCategory === 'create'"
					@click="switchView('create')"
				>
					Создать
				</button-vue>
				<button-vue
					:color="'gray'"
					:active="activeNestedDashboardCategory === 'list'"
					@click="switchView('list')"
				>
					Список
				</button-vue>
			</div>
		</div>
		<section class="dashboard__section">
			<keep-alive>
				<component
					:is="activeComponent"
					ref="componentRef"
					class="animate__animated animate__fadeIn"
				/>
			</keep-alive>
			<vue-element-loading 
				:active="isLoading"
				:background-color="'rgba(255, 255, 255, .2)'"
				:color="'blue'"
			/>
		</section>
	</div>
</template>

<script>
import ButtonVue from '@/components/BaseButton.vue';
import switchDashboardCategoryMixin from '@/mixins/switchDashboardCategoryMixin';
export default {
	components: {
		ButtonVue
	},
	mixins: [switchDashboardCategoryMixin],
	data() {
		return {
			isLoading: false
		};
	},
	watch: {
		activeNestedDashboardCategory() {
			this.setActiveComponent(true);
		}
	},
	created() {
		this.setActiveComponent(true);
	},
	methods: {
		switchView(route) {
			this.$store.commit('setActiveNestedDashboardCategory', route);
		},
	},
};
</script>

<style lang="scss" scoped>
	.dashboard {
		&__header {
			display: flex;
			justify-content: space-between;
			height: 6rem;
		}
		&__title {
			align-self: center;
			font-weight: 600;
			color: #000000;
		}
		&__control {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		&__section {
			width: 100%;
			min-height: 70vh;
			border-radius: 9px;
			background-color: #ffffff;
		}
	}
</style>