import { mapGetters } from 'vuex';
export default {
	data() {
		return {
			activeComponent: null,
			activeDashboardTitle: '',
			componentsImports: {
				templates: {
					create: () => import('@/components/TemplateCreate.vue'),
					list: () => import('@/components/TemplateList.vue'),
				},
				reports: {
					list: () => import('@/components/ReportList.vue'),
				},
				clients: {
					create: () => import('@/components/ClientCreate.vue'),
					list: () => import('@/components/ClientList.vue'),
					clientsTemplates: {
						create: () => import('@/components/TemplateCreate.vue'),
						list: () => import('@/components/TemplateList.vue'),
					},
				}
			},
			componentsTitles: {
				templates: {
					create: 'Создание шаблона',
					list: 'Список шаблонов',
				},
				reports: {
					create: 'Создание отчета',
					list: 'Список отчетов',
				},
				clients: {
					create: 'Создание клиента',
					list: 'Список клиентов',
					clientsTemplates: {
						create: `Создание шаблона ${this.$route.params.userId}`,
						list: `Список шаблонов ${this.$route.params.userId}`,
					},
				},
			}
		};
	},
	computed: {
		...mapGetters([
			'getActiveDashboard',
			'getActiveDashboardCategory',
			'getActiveNestedDashboard',
			'getActiveNestedDashboardCategory'
		]),
		activeDashboard() {
			return this.getActiveDashboard;
		},
		activeDashboardCategory() {
			return this.getActiveDashboardCategory;
		},
		activeNestedDashboard() {
			return this.getActiveNestedDashboard;
		},
		activeNestedDashboardCategory() {
			return this.getActiveNestedDashboardCategory;
		}
	},
	methods: {
		setActiveComponent(nested = false) {
			if(!this.activeDashboard) return;
			this.isLoading = true;
			let component = null;
			if(nested) component = this.componentsImports[this.activeDashboard][this.activeNestedDashboard][this.activeNestedDashboardCategory];
			else component = this.componentsImports[this.activeDashboard][this.activeDashboardCategory];
			component().then((loadedComponent) => {
				this.activeComponent = loadedComponent.default || loadedComponent;
				this.isLoading = false;
				this.setActiveComponentTitle(nested);
			});
		},
		setActiveComponentTitle(nested = false) {
			if(nested) this.activeDashboardTitle = this.componentsTitles[this.activeDashboard][this.activeNestedDashboard][this.activeNestedDashboardCategory];
			else this.activeDashboardTitle = this.componentsTitles[this.activeDashboard][this.activeDashboardCategory];
		},
	},
};